import { mapGetters } from 'vuex'
import {getDeckList, getUserCollectionDecks, addCustomerSetting, getCustomerSetting, updateCustomerSetting} from "@/api/dbapi";
import NavBar from '@/components/NavBar'

export default {
  components: {
    NavBar,
  },
  data() {
    return {
      videoAd: null,
      showVideoAdBtn: true,
      deckList: [],
      logo: 'https://cloud-minapp-18282.cloud.ifanrusercontent.com/1g9QyXTPpyOMVypO.png',
      codeImg: 'https://cloud-minapp-18282.cloud.ifanrusercontent.com/1g9oGSrxprEojAfB.jpg',
    }
  },
  computed: {
    ...mapGetters([
      'userInfo',
      'navHeight'
    ]),
    getDeckList() {
      return this.deckList
    }
  },
  methods: {
    userInfoHandler(data) {
      this.$store.dispatch('getUserInfo', data).then(res => {
        this.genUserCollection()
      }).catch(err => {
        console.log(err)
      })
    },
    genUserCollection() {
      if (this.userInfo.id) {
        wx.showNavigationBarLoading();
        this.$store.dispatch('getCollectedDecks', this.userInfo.id).then(res => {
          this.deckList = res.list.filter(val => {
            return !(!val || val === "");
          })
          wx.hideNavigationBarLoading()
          wx.stopPullDownRefresh();
        }).catch(err => {
          console.log(err)
          wx.hideNavigationBarLoading()
          wx.stopPullDownRefresh();
        })
      } else {
        wx.stopPullDownRefresh();
      }
    },
    handleDeckClick(item) {
      let url = ''
      if (item.trending) {
        url = `/pages/decks/deckDetail/index?id=${item.id}&trending=1`
      } else {
        url = `/pages/decks/deckDetail/index?id=${item.id}`
      }
      wx.navigateTo({
        url: url
      })
    },
    handleAboutClick() {
      wx.navigateTo({
        url: `/pages/mine/about/index`
      })
    },
    handleCollectionClick() {
      if (this.userInfo.id) {
        wx.navigateTo({
          url: `/pages/mine/collection/index`
        })
      } else {
        wx.showToast({
          title: '请登录',
          icon: 'none'
        })
      }
    },
    handleHSVisionClick() {
      wx.navigateToMiniProgram({
        appId: 'wx010ca9734f850748',
        path: `/pages/index/index`,
        success(res) {
          // 打开成功
          console.log(res)
        }
      })
    },
    handleClickCodeImg() {
      wx.previewImage({
        urls: [this.codeImg]
      })
    },
    handleCopyBtn() {
      wx.setClipboardData({
        data: '558391916',
        success: function (res) {
          wx.showToast({
            title: '打开支付宝，首页搜索栏中直接粘贴，即可领取每日红包，金额随机',
            icon: 'none',
            duration: 2500,
          })
        }
      })
    },
    async handleVideoClick() {
      let videoAdUseable = true//wx.canIUse('createRewardedVideoAd')
      if (videoAdUseable) {
        if (this.videoAd) {
          this.videoAd.show().catch(() => {
            // 失败重试
            this.videoAd.load()
              .then(() => videoAd.show())
              .catch(err => {
                console.log('激励视频 广告显示失败', err)
              })
          })
        }
      } else {
        wx.showToast({
          title: '微信版本过低，无法播放激励视频',
          icon: 'none',
          duration: 2500
        })
      }
    }
  },
  onLoad() {
    if (wx.createRewardedVideoAd) {
      this.videoAd = wx.createRewardedVideoAd({
        adUnitId: 'adunit-e8e54c4290dbf4ad'
      })
      this.videoAd.onClose(async (status) => {
        console.log('激励视频关闭', status)
        if (status && status.isEnded || status === undefined) {
          let res = await getCustomerSetting(this.userInfo.id)
          console.log(res)
          let temp = ''
          if (res.meta.total_count > 0) {
            let ad_click_num = res.objects[0].ad_click_num + 1
            temp = await updateCustomerSetting({
              ad_click_num: ad_click_num,
            }, res.objects[0].id)
          } else {
            temp = await addCustomerSetting({}, this.userInfo.id)
          }
          console.log(temp)
          let now = new Date()
          try {
            wx.setStorageSync('ads_video_date', new Date(now.getFullYear(), now.getMonth(), now.getDate()).getTime()/1000)
          } catch (e) {
            console.log(e)
          }
          wx.showToast({
            title: '感谢！',
            icon: 'none',
            duration: 2500
          })
        } else {
          wx.showToast({
            title: '没有完整播放视频哦，喵。。。',
            icon: 'none',
            duration: 2500
          })
        }
      })
      this.videoAd.onError((res) => {
        console.log('激励视频错误', res)
        wx.showToast({
          title: '出了点小问题，无法播放激励视频',
          icon: 'none',
          duration: 2500
        })
      })
    }
  },
  onShow() {
    // console.log('onShow', this.$store.state.cards.collectedDecks)
    // this.deckList = this.$store.state.cards.collectedDecks
    this.genUserCollection()
  },
  onShareAppMessage(res) {
    return {
      title: '炉石传说情报站',
      path: '/pages/index/index'
    }
  },
  onPullDownRefresh() {
    this.genUserCollection()
  },
}
